import axios from 'axios';
import { BASE_PATH } from '@/consts';

export interface Chapter {
  id: string;
  type: string;
  attributes: {
    title: string;
    content?: string;
  };
}

export interface BookDetails {
  id: string;
  type: string;
  attributes: {
    title: string;
    author: string;
    cover: string;
    pages: number;
    release_date: string;
    summary: string;
    chapters: Chapter[];
  };
}

export interface ApiResponse {
  data: BookDetails;
}

export const fetchBookDetails = async (id: string): Promise<BookDetails | null> => {
  try {
    const response = await axios.get<ApiResponse>(`${BASE_PATH}/books/${id}`);
    return response.data.data;
  } catch (error) {
    console.error('Error fetching book details:', error);
    return null;
  }
};